import React from 'react';
import ChatBot from 'react-simple-chatbot';

const helloWorldCode =
`import ChatBot from 'react-simple-chatbot';

<ChatBot
  steps={[
    {
      id: 'hello-world',
      message: 'Hello World!',
      end: true,
    },
  ]}
/>
`;

const HelloWorld = () => (
  <div className="docs-hello-world">
    <p>Check the hello world code</p>
    <pre>
      <code>
        { helloWorldCode }
      </code>
    </pre>
    <p>Result</p>
    <ChatBot
      steps={[
        {
          id: 'hello-world',
          message: 'Hello World!',
          end: true,
        },
      ]}
    />
  </div>
);

export default HelloWorld;



