import React from 'react';
import ChatBot from 'react-simple-chatbot';

const exampleCode =
`<ChatBot
  steps={[
    {
      id: '1',
      message: 'Please type a number',
      trigger: '2',
    },
    {
      id: '2',
      user: true,
      type: true,
      validator: (value) => {
        if (isNaN(value)) {
          return 'value should be a number';
        }
        return true;
      },
      trigger: '1',
    },
  ]}
/>
`;

const Example2 = () => (
  <div className="docs-example-2">
    <ChatBot
      steps={[
        {
          id: '1',
          message: 'Please type a number',
          trigger: '2',
        },
        {
          id: '2',
          user: true,
          type: true,
          validator: (value) => {
            if (isNaN(value)) {
              return 'value should be a number';
            }
            return true;
          },
          trigger: '1',
        },
      ]}
    />
    <h3 style={{ marginTop: 20 }}>Code</h3>
    <pre>
      <code>
        {exampleCode}
      </code>
    </pre>
  </div>
);

export default Example2;



