import React from 'react';
import ChatBot from 'react-simple-chatbot';

const exampleCode =
`<ChatBot
  steps={[
    {
      id: '1',
      message: 'Please type a number',
      trigger: '2',
    },
    {
      id: '2',
      component: (
        <div> This is a example component </div>
      ),
      end: true,
    },
  ]}
/>
`;

const Example3 = () => (
  <div className="docs-example-3">
    <ChatBot
      steps={[
        {
          id: '1',
          message: 'Please type a number',
          trigger: '2',
        },
        {
          id: '2',
          component: (
            <div> This is a example component </div>
          ),
          end: true,
        },
      ]}
    />
    <h3 style={{ marginTop: 20 }}>Code</h3>
    <pre>
      <code>
        {exampleCode}
      </code>
    </pre>
  </div>
);

export default Example3;



